import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import Sidebar from "../../components/Siderbar";
import Header from "../../components/HeaderCadastrarProd";

export default function ProdutoDetalhes() {
  const router = useRouter();
  const { id } = router.query;

  const [produto, setProduto] = useState<any>(null);

  useEffect(() => {
    if (id) {
      // Aqui você buscaria os dados da API com base no ID
      // Exemplo mockado:
      setProduto({
        id,
        nome: "Garrafa Térmica",
        categoria: "Garrafa",
        cor: "Azul",
        capacidade: "1,2L"
      });
    }
  }, [id]);

  if (!produto) return <div className="p-8">Carregando...</div>;

  return (
    <div className="flex">
      <Sidebar />
      <main className="flex-1 p-8">
        <Header />
        <h1 className="text-2xl font-bold mb-6">Detalhes do Produto</h1>

        <div className="space-y-4">
          <p><strong>Nome:</strong> {produto.nome}</p>
          <p><strong>Categoria:</strong> {produto.categoria}</p>
          <p><strong>Cor:</strong> {produto.cor}</p>
          <p><strong>Capacidade:</strong> {produto.capacidade}</p>
        </div>

        <div className="flex justify-end mt-8 gap-4">
          <button
            onClick={() => router.push(`/produtos/${id}/editar`)}
            className="bg-yellow-500 text-white px-4 py-2 rounded hover:bg-yellow-600"
          >
            Atualizar
          </button>
          <button
            onClick={() => {
              // lógica de delete aqui
              alert("Produto apagado!");
              router.push("/produtos");
            }}
            className="bg-red-600 text-white px-4 py-2 rounded hover:bg-red-700"
          >
            Apagar
          </button>
        </div>
      </main>
    </div>
  );
}
